/* index.css or a similar global stylesheet */
body, html, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow: hidden; /* Prevents scrolling on the entire page */
  background: linear-gradient(270deg, #ff7e5f, #feb47b, #ff6f69, #ffcc5c);
  background-size: 200% 200%;
  animation: gradientAnimation 12s ease infinite;
}

.App {
  padding: 20px;
  color: white;
  min-height: 100vh; /* Ensure .App takes full viewport height */
  overflow-y: auto;
}

@keyframes gradientAnimation {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}